      <div class="filter">
        <button class="filter-btn" @click="showPriceFilterBar">Price</button>
        <div class="filter-bar" v-if="showPriceFilter">
          <vue-range-slider
            v-model="priceFilterValue"
            :min="min"
            :max="max"
            :formatter="formatter"
            :tooltip-merge="tooltipMerge"
            :enable-cross="enableCross"
          ></vue-range-slider>
          <div class="price-range-show">
            <div class="range-show">
              <span class="range-show-label">
                Min price:
              </span>
              <span class="range-show-value"> {{ priceFilterValue[0] }}$ </span>
            </div>
            <div class="range-show">
