<script>
import "vue-range-component/dist/vue-range-slider.css";
import VueRangeSlider from "vue-range-component";
import { SweetModal, SweetModalTab } from "sweet-modal-vue";

export default {
  components: {
    VueRangeSlider,
    SweetModal,
    SweetModalTab,
  },
  data() {
    return {
      priceFilterValue: [100, 1000],
      showTypeFilter: false,
      showPriceFilter: false,
    };
  },
  methods: {
    closeAllFilters() {
      this.showTypeFilter = false;
      this.showPriceFilter = false;
    },
    showTypeFilterBar() {
      // this.closeAllFilters();
      this.showPriceFilter = false;
      this.showTypeFilter = !this.showTypeFilter;
    },
    showPriceFilterBar() {
      // this.closeAllFilters()
      this.showTypeFilter = false;
      this.showPriceFilter = !this.showPriceFilter;
    },
    showModalFeatures() {
      this.closeAllFilters()
      this.$refs.modalFeatures.open();
    },
    closeModalFeatures() {
      this.$refs.modalFeatures.close();
    },
  },
  created() {
    this.min = 100;
    this.max = 2500;
    this.enableCross = false;
    this.tooltipMerge = false;
    this.formatter = (value) => `$${value}`;
  },
};
</script>

<style scoped lang="scss">
.filters-container {
  margin: 1rem 0;
  display: flex;
  .filter {
    margin-right: 0.5rem;
    position: relative;
  }
}

.filter-bar {
  position: absolute;
  top: 45px;
  left: 6px;
  border: 2px solid $lightBackground;
  width: 250px;
  z-index: 999;
  background-color: $white;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0px 0px 8px 3px rgba(217, 211, 211, 0.4);
}
.bar-button {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  margin-top: 1rem;
  button {
    color: $white;
    border: none;
    background-color: $textDark;
    transition: all 0.4s;
    padding: 0.5rem 1rem;
    border-radius: 0.2rem;
    // &:hover {
    //   background-color: $primaryDark;
    // }
  }
}
.price-range-show {
  margin-top: 1rem;
  .range-show-label {
    color: $textDark;
  }
  .range-show-value {
    color: $text;
  }
}
/* Customize the label (the container) */
.check-container {
  display: block;
  position: relative;
  padding-left: 1.7rem;
  cursor: pointer;
  font-size: 1.2rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin-bottom: 0.5rem;
}

/* Hide the browser's default checkbox */
.check-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 6px;
  left: 0;
  height: 20px;
  width: 20px;
  border: 1px solid $text;
  border-radius: 5px;
}

/* On mouse-over, add a grey background color */
.check-container:hover input ~ .checkmark {
  border-color: $textDark;
}

/* When the checkbox is checked, add a blue background */
.check-container input:checked ~ .checkmark {
  background-color: $primary;
  border: none;
  color: $white;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.check-container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.check-container .checkmark:after {
  left: 7px;
  top: 4px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.filter-btn {
  border: 2px solid $text;
  border-radius: 30px;
  padding: 0.3rem 1rem;
  color: $textDark;
  background-color: transparent;
  font-size: 1.2rem;
  &:hover {
    border: 2px solid $textDark;
  }
}
.formulate-input {
  margin-bottom: 0.5rem !important;
}

.f-bar {
  width: 400px;
}
</style>
